@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap');

body {
  font-family: 'poppins';
  font-size: 25px;
  /* Auto layout */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0px;

  position: relative;

  background: #FFFFFF;
}

/* Setting the root element to be a flex container with a row direction, aligning the items to the top,
and setting the width and height. */
#root {
  /* Auto layout */

  display: flex;
  flex-direction: row;
  align-items: flex-start;
  padding: 0px;
  height: 100vh;
  /* Inside auto layout */

  flex: none;
  order: 0;
  align-self: stretch;
  flex-grow: 1;
}

/* This is setting the font weight, size, line height, margin, text transform, and color for the h1
tag. */
h1 {
    font-weight: 700;
    font-size: 36px;
    line-height: 54px;
    margin: 0;

    text-transform: capitalize;
}

h2 {
  font-weight: 700;
  font-size: 31px;
  line-height: 159%;
  /* identical to box height */

  text-transform: capitalize;
}

/* This is setting the font weight, size, line height, margin, and color for the h2 tag. */
h3 {
    font-weight: 400;
    font-size: 18.5px;
    line-height: 28px;
    margin: 0;    
}

/* Setting the margin to 0 for all p tags. */
p{
    margin: 0;
}

/* Making the cursor change to a pointer when hovering over buttons. */
button{
  cursor: pointer;
}

/* Styling all primary buttons */
.primaryButton {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 13.6698px 34.9867px;
  gap: 8.46px;
  
  background: #302CCC;
  border-radius: 9.54184px;
  border: 0;

  font-weight: 700;
  font-size: 15.9031px;
  line-height: 24px;
  /* identical to box height */

  text-transform: capitalize;

  color: #EEE3E2;
}

.primaryButton:hover {
  background: #2926A1;
}

.primaryButton:focus {
  background: #212063;
}

.primaryButton:active {
  background: #1F1F3B;
}
